<template>
  <el-container>
    <el-header style="width: 200px;height: 300px">
      <accidentFilter @indexSelect="listByFilter" ref="accidentFilter"></accidentFilter>
    </el-header>
    <el-main>
      <accidents class="accidents-area" ref="accidentsArea"></accidents>
    </el-main>
  </el-container>
</template>

<script>
  import AccidentFilter from './AccidentFilter'
  import Accidents from './Accidents'

  export default {
    name: 'AccidentIndex',
    components: {AccidentFilter, Accidents},
    methods: {
      listByFilter () {
        var _this = this
        var did = this.$refs.accidentFilter.did
        var tid = this.$refs.accidentFilter.tid
        var oid = this.$refs.accidentFilter.oid
        var nid = this.$refs.accidentFilter.nid
        var aid = this.$refs.accidentFilter.aid
        var url = 'filter/' + did + '/' + tid + '/' + oid + '/' + nid + '/' + aid + '/accidents'
        this.$axios.get(url).then(resp => {
          if (resp && resp.data.code === 200) {
            _this.$refs.accidentArea.accidents = resp.data.result
            _this.$refs.accidentsArea.currentPage = 1
          }
        })
      }
    }
  }
</script>

<style scoped>
  .accidents-area {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
</style>



