<template>
  <div style="margin-top: 40px">
    <div class="accidents-area">
     <el-row>
        <el-col
          v-for="accident in accidents"
          :key="accident.id"
          :span="6"
        >
          <el-card :span="8" :body-style="{ padding: '0px' }" class="box-card"  shadow="hover">
           <router-link class="accident-link" :to="{path:'accidents/details',query:{id: accident.id}}">
            <span style="font-size: 20px">ID:{{accident.id}}</span></router-link>
            <div><span style="font-size: 20px">{{accident.intro}}</span></div>
            <video controls width="210" :src="accident.cover" type="video/mp4"></video>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-pagination
      background
      layout="total, prev, pager, next, jumper"
      @current-change="handleCurrentChange"
      :page-size="pageSize"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>

  export default {
      name: 'Accidents',
      data () {
        return {
          accidents: [],
          pageSize: 12,
          total: 0
          }
        },
      mounted: function () {
        this.loadAccidents()
      },
      methods: {
        loadAccidents () {
          var _this = this
          this.$axios.get('/accidents/' + this.pageSize + '/1').then(resp => {
            console.log(_this.accident)
            if (resp && resp.data.code === 200) {
              _this.accidents = resp.data.result.content
              _this.total = resp.data.result.totalElements
              console.log(_this.accidents[0])
            }
          })
        },
        handleCurrentChange (page) {
          var _this = this
          this.$axios.get('/accidents/' + this.pageSize + '/' + page).then(resp => {
            if (resp && resp.data.code === 200) {
              _this.accidents = resp.data.result.content
              _this.total = resp.data.result.totalElements
            }
          })
        }
      }
    }
</script>

<style scoped>
.accidents-area {
  width: 1200px;
  height: 700px;
  margin-left: auto;
  margin-right: auto;
}
.accident-link {
  text-decoration: none;
  color: #606266;
}
.accident-link:hover {
  color: #409EFF;
}
.head{
  height: 50px;
}
.box-card {
  width: 240px;
  height: 200px;
}
.button-style {
  margin-top: 24px;
}
</style>



